body.cookie-is-open {
  .privacy {
    transform: translateY(100px);
    margin-bottom: 200px;
    @media (max-width: 767px) {
      transform: translateY(0);
      margin-bottom: 0;
    }
    .scroll-prompt {
      bottom: 150px;
    }
    .scroll-prompt-text {
      bottom: 115px;
    }
  }
}


.privacy {
  text-align: center;
  max-width: 1024px;
  margin: 0 auto;
  .scroll-prompt,
  .scroll-prompt-text {
    display: block;
    @media (max-width: 992px) {
      display: none;
    }
  }
}

.privacy-intro {
  position: relative;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: 992px) {
    height: auto;
  }
  @media (max-height: 625px) and (min-width: 767px) {
    height: auto;
    margin-top: 100px;
    .scroll-prompt, .scroll-prompt-text {
      display: none !important;
    }
  }
}

.privacy-title {
  margin-bottom: 40px;
  text-align: center;
  @media (max-width: 992px) {
    margin-top: 125px;
  }
  @media (max-width: 767px) {
    margin-top: 65px;
    padding: 0 15px;
  }
}

.privacy-lead {
  font-family: $open-sans;
  font-size: 16px;
  font-weight: lighter;
  color: #1C1D21;
  line-height: 26px;
  max-width: 625px;
  margin: 0 auto;
  @media (max-width: 767px) {
    padding: 0 15px;
    font-size: 14px;
    line-height: 24px;
  }
}

.privacy-icons {
  display: block;
  margin: 50px auto 100px auto;
  position: relative;
  right: 1px;
  img {
    width: 150px;
    height: 150px;
  }
  @media (max-width: 992px) {
    padding: 0 50px;
    margin-bottom: 50px;
    right: 0;
  }
  @media (max-width: 767px) {
    padding: 0;
    margin-bottom: 80px;
  }
}

.privacy-icon {
  vertical-align: top;
  opacity: 0;
  text-align: center;
  display: inline-block;
  animation-name: basic-opacity;
  animation-duration: 0.8s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  width: 170px;
  @media (max-width: 767px) {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
  }
  &:nth-child(2) {
    animation-delay: 0.5s;
  }
  &:last-of-type {
    animation-delay: 1s;
  }
}

.privacy-icon + .privacy-icon {
  margin-left: 80px;
  @media (max-width: 992px) {
    margin-left: 30px;
  }
  @media (max-width: 767px) {
    margin-left: auto;
    margin-right: auto;
  }
}

.privacy-icon-title {
  margin: 20px auto 0 auto;
  font-family: $roboto-mono;
  font-size: 12px;
  font-weight: lighter;
  color: $primary;
  line-height: 22px;
  max-width: 200px;

  @media (max-width: 767px) {
    font-size: 14px;
    margin-top: 10px;
    margin-bottom: 30px;
  }
}

.privacy-content {
  text-align: left;
}

.privacy-content-segment {
  max-width: 642px;
  margin: 0 auto 80px auto;
  text-align: left;
  font-family: $open-sans;
  font-weight: lighter;
  font-size: 16px;
  line-height: 26px;
  @media (max-width: 992px) {
    padding: 0 50px;
  }
  @media (max-width: 767px) {
    font-size: 14px;
    line-height: 24px;
    padding: 0 15px;
    text-align: center;
    p {
      margin-bottom: 20px;
    }
  }
  p {
    margin-top: 0;
    margin-bottom: 40px;
  }
  &.first-segment {
    margin-top: 40px;
    @media (max-width: 767px) {
      margin-top: 0;
    }
  }
  h2 {
    font-weight: lighter;
    font-size: 24px;
    color: $primary;
    margin: 0 0 40px 0;
    @media (max-width: 767px) {
      margin-bottom: 20px;
    }
  }
  h3 {
    margin: 0;
    color: $primary;
    font-weight: lighter;
    @media (max-width: 767px) {
      font-size: 14px;
    }
  }
}

.promise-heading {
  font-weight: lighter;
  font-size: 24px;
  color: $primary;
  margin: 0 0 40px 0;
  @media (max-width: 767px) {
    margin-bottom: 20px;
  }
}

.promise-subheading {
  margin: 0;
  color: $primary;
  font-weight: lighter;
}

.privacy-item {
  font-weight: lighter;
  line-height: 26px;
  margin-bottom: 40px;
  p {
    margin: 0;
  }
  &.has-icon {
    position: relative;
    padding-left: 80px;
    text-align: left;
    @media (max-width: 767px) {
      padding-left: 64px;
      text-align: left;
    }
    &::before {
      position: absolute;
      top: 9px;
      left: 0;
      content: '';
      width: 52px;
      height: 52px;
      display: block;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
    }
  }
  &.privacy-point-1 {
    &::before {
      background-image: url('../images/privacy-point-1.svg');
    }
  }
  &.privacy-point-2 {
    &::before {
      background-image: url('../images/privacy-point-2.svg');
    }
  }
  &.privacy-point-3 {
    &::before {
      background-image: url('../images/privacy-point-3.svg');
    }
  }
  &.privacy-point-4 {
    &::before {
      background-image: url('../images/privacy-point-4.svg');
    }
  }
  &.privacy-point-5 {
    &::before {
      background-image: url('../images/privacy-point-5.svg');
    }
  }
  &.privacy-point-6 {
    &::before {
      background-image: url('../images/privacy-point-6.svg');
    }
  }
  &.privacy-point-7 {
    &::before {
      background-image: url('../images/privacy-point-7.svg');
    }
  }
}

.tooltip-icon {
  display: block;
  width: 16px;
  height: 16px;
  background-image: url('../images/tooltip-icon.png');
  background-size: contain;
  background-repeat: no-repeat;
}

.privacy-info {
  margin-top: 40px;
  margin-bottom: 100px;
}

.privacy-info-item {
  position: relative;
  font-size: 16px;
  font-family: $open-sans;
  font-weight: lighter;
  padding-left: 140px;
  line-height: 26px;
  margin-bottom: 35px;
  text-align: left;
  @media (max-width: 992px) {
    padding-left: 20px;
  }
  @media (max-width: 767px) {
    text-align: left;
    font-size: 14px;
    line-height: 24px;
  }
  p {
    margin: 0 0 35px 0;
  }
}

.privacy-info-title {
  margin: 0;
  font-family: $roboto-mono;
  color: $primary;
  font-weight: lighter;
}

.tooltip-icon {
  position: absolute;
  top: 6px;
  left: 118px;
  cursor: pointer;
  @media (max-width: 992px) {
    left: -1px;
  }
}

.tooltip-content {
  display: block;
  position: absolute;
  top: -8px;
  left: -226px;
  width: 206px;
  height: auto;
  font-size: 12px;
  background-color: $primary;
  color: white;
  box-shadow: 0 2px 4px 0 rgba(44,65,198,0.50);
  padding: 5px 10px;
  line-height: 1.5;
  &.is-hidden {
    display: none;
  }
  @media (max-width: 992px) {
    display: none;
    &.is-active {
      display: block;
    }
    &.one-line {
      top: -39px;
    }
    &.three-lines {
      top: -76px;
    }
    &.four-lines {
      top: -92px;
    }
  }
  @media (max-width: 992px) {
    top: -60px;
    left: 2px;
  }
  &::after {
    content: '';
    display: block;
    position: absolute;
    top: 10px;
    right: -6px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5px 0 5px 6px;
    border-color: transparent transparent transparent $primary;
    @media (max-width: 992px) {
      top: initial;
      right: initial;
      bottom: -8px;
      left: 2px;
      transform: rotate(90deg);
    }
  }
}
