body.cookie-is-open {
  .contact-heading {
    margin-top: 273px;
    @media (max-width: 767px) {
      margin-top: 65px;
    }
  }
}

.contact-intro {
  text-align: center;
  p {
    font-family: $open-sans;
    font-size: 16px;
    color: #1C1D21;
    line-height: 26px;
    font-weight: lighter;
    @media (max-width: 767px) {
      font-size: 14px;
      line-height: 24px;
      padding: 0 35px;
      margin: 0;
    }
  }
}

.contact-heading {
  max-width: 662px;
  margin: 173px auto 40px auto;
  animation-duration: 1s;
  @media (max-width: 767px) {
    padding: 0 15px;
    margin: 65px auto 40px auto;
  }
}

.contact-subtitle {
  animation-delay: 3s;
  animation-duration: 2s;
}

.contact-form {
  text-align: center;
  max-width: 781px;
  margin: 110px auto 0 auto;
  @media (max-width: 992px) {
    margin-top: 60px;
  }
}

.form-test-heading {
  font-weight: lighter;
  font-size: 16px;
  margin-top: 40px;
  margin-bottom: 25px;
  @media (max-width: 767px) {
    margin: 0 auto 25px auto;
    max-width: 269px;
  }
}

.contact-form-columns {
  text-align: left;
  display: flex;
  height: 344px;
  @media (max-width: 992px) {
    height: auto;
    padding: 0 15px;
    flex-direction: column;
    margin-bottom: 50px;
  }
}

.contact-form-col {
  width: 50%;
  &:first-child {
    margin-right: 20px;
    padding-top: 6px;
    @media (max-width: 992px) {
      margin: 0 auto;
    }
  }
  @media (max-width: 992px) {
    margin: 0 auto;
    &:last-of-type {
      margin-top: 50px;
    }
  }
  @media (max-width: 767px) {
    max-width: 280px;
    width: 100%;
    &:last-of-type {
      margin-top: 0;
    }
  }
}

.error-message {
  position: absolute;
  bottom: -19px;
  left: 2px;
  font-family: $roboto-mono;
  font-size: 14px;
  font-weight: lighter;
  color: $secondary-red;
  opacity: 0;
  visibility: hidden;
  &.is-visible {
    opacity: 1;
    visibility: visible;
  }
}

.contact-input-container {
  position: relative;
}

.input-tooltip {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 16px;
  right: 0;
  display: block;
  width: 25px;
  height: 38px;
  background-image: url('../images/input-tooltip.svg');
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
  &.is-active {
    opacity: 1;
    visibility: visible;
  }
  @media (min-width: 992px) {
    &:hover {
      .input-tooltip-content {
        visibility: visible;
        position: auto;
        opacity: 1;
      }
    }
  }
}

.input-tooltip-content {
  font-weight: lighter;
  font-size: 12px;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 1;
  display: block;
  position: absolute;
  top: -96px;
  right: -8px;
  width: 220px;
  height: auto;
  font-family: $open-sans;
  background-color: $primary;
  color: white;
  box-shadow: 0 2px 4px 0 rgba(44,65,198,0.50);
  padding: 15px 12px 12px 15px;
  line-height: 1.5;
  &.is-visible {
    opacity: 1;
    visibility: visible;
  }
  &::after {
    content: '';
    display: block;
    position: absolute;
    right: 18px;
    bottom: -9px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 6px 0 6px 7px;
    border-color: transparent transparent transparent $primary;
    transform: rotate(90deg);
  }
   p {
     margin: 0;
   }
}

.contact-input {
  height: 35px;
  width: 100%;
  margin-bottom: 32px;
  border-left-color: transparent;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: #D8D8D8;
  font-weight: lighter;
  font-size: 16px;
  color: #9B9B9B;
  border-radius: 0;
  padding-top: 7px;
  &:focus {
    outline: 0;
  }
  &:last-of-type {
    margin-bottom: 0;
  }
}

.textarea-container {
  position: relative;
  .input-tooltip {
    top: 22px;
  }
  .contact-label {
    position: absolute;
    top: 0;
    left: 0;
    background: white;
    width: 100%;
    padding-left: 5px;
  }
  @media (max-width: 767px) {
    margin-top: 50px;
  }
}

.contact-textarea {
  height: 327px;
  width: 100%;
  padding-top: 25px;
  padding-left: 5px;
  padding-right: 29px;
  border: 0;
  font-weight: lighter;
  font-size: 16px;
  color: #9B9B9B;
  resize: none;
  background-position-y: 31px;
  background-image: linear-gradient(white, white 30px, #D8D8D8 30px, #D8D8D8 31px, white 31px);
  background-size: 100% 44px;
  line-height: 43px;
  @-moz-document url-prefix() {
    background-position-y: 32px;
  }
  @media (max-width: 767px) {
    line-height: 43px;
  }
  &:focus {
    outline: 0;
  }
  &::placeholder {
    color: #9B9B9B;
    font-weight: lighter;
    line-height: 43px;
    opacity: 1;
  }
}

.textarea-counter {
  font-family: $roboto-mono;
  font-size: 14px;
  color: #9B9B9B;
  float: right;
  font-weight: lighter;
  margin-top: 3px;
  &.is-valid {
    color: $secondary-green;
  }
  &.is-invalid {
    color: $secondary-red;
  }
}

.contact-label {
  font-weight: lighter;
  font-size: 14px;
  color: #9B9B9B;
}

.contact-label-2 {
  display: block;
  margin-top: 32px;
  line-height: 1.5;
  @media (max-width: 767px) {
    margin-top: 32px;
  }
}

.contact-form-test {
  display: inline-block;
  position: relative;
  text-align: center;
  font-size: 16px;
  color: $primary;
  font-weight: lighter;
}

.form-test-icon {
  position: absolute;
  top: 100px;
  right: 47px;
  display: block;
  width: 26px;
  height: 26px;
  background-repeat: no-repeat;
  background-size: contain;
  &.is-valid {
    background-image: url('../images/human-test-icon-valid.svg');
  }
  &.is-invalid {
    background-image: url('../images/human-test-icon-invalid.svg');
  }
  @media (max-width: 767px) {
    top: 79px;
    right: 13px;
  }
}

.test-number-card,
.test-input {
  display: inline-block;
  width: 40px;
  height: 50px;
  border: 1px solid #D8D8D8;
  padding-top: 13px;
  vertical-align: middle;
}

.test-input {
  padding-top: 0;
  padding-left: 14px;
  border-radius: 0;
  -webkit-appearance: none;
  border-color: $primary;
  &:focus {
    outline: 0;
  }
  &.is-valid {
    border-color: $secondary-green;
  }
  &.is-invalid {
    border-color: $secondary-red;
  }
}

.form-text-message {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  bottom: -39px;
  left: 0;
  right: 0;
  display: block;
  margin-top: 14px;
  &.is-valid {
    opacity: 1;
    visibility: visible;
    color: $secondary-green;
  }
  &.is-invalid {
    opacity: 1;
    visibility: visible;
    color: $secondary-red;
  }
}

.contact-form-submit {
  margin: 60px auto 100px auto;
  display: block;
  border: 1px solid $primary;
  color: $primary;
  font-size: 20px;
  width: 140px;
  height: 50px;
  background-color: white;
  pointer-events: auto;
  &:hover {
    border-width: 2px;
  }
  &:active {
    background-color: #E0E3F7;
  }
  &:focus {
    outline: 0;
  }
  &.is-inactive {
    pointer-events: none;
    color: #D8D8D8;
    border-color: #D8D8D8;
  }
}

.contact-confirmation {
  display: none;
}

.spinner-wrapper {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  @media (max-width: 767px) {
    margin-top: -65px;
  }
}

.spinner-text {
  color: $primary;
  font-size: 20px;
  font-weight: lighter;
  text-align: center;
  position: relative;
  top: 84px;
  left: 12px;
}

.spinner-image-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.spinner-image {
  background-image: url('../images/spinner-image.svg');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  width: 135px;
  height: 99px;
  display: block;
  margin-top: 64px;
  @media (max-width: 767px) {
    width: 101px;
    height: 62px;
    background-size: contain;
  }
}

.spinner-dot {
  display: block;
  width: 7px;
  height: 7px;
  background-color: $primary;
  border-radius: 50%;
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #2c41c6;
  margin: 0;
  position: relative;
  z-index: 2;
  top: 22px;
  left: 22px;
  @media (max-width: 767px) {
    height: 6px;
    width: 6px;
    top: 13px;
    left: 18px;
  }
}

@keyframes rotate {
	from { transform: rotate(0deg);   }
	to   { transform: rotate(360deg); }
}

.spinner {
	animation: rotate 1.3s linear infinite;
  background: #473CCB;
	border-radius: 50%;
	height: 200px;
	width: 200px;
	position: relative;
  @media (max-width: 767px) {
    width: 156px;
    height: 156px;
  }
}

.spinner:before,
.spinner:after {
	content: '';
	position: absolute;
}

.spinner:before {
	border-radius: 50%;
	background:
		linear-gradient(0deg,   hsla(0, 0%, 100%, 1  ) 50%, hsla(0, 0%, 100%, 0.9) 100%)   0%   0%,
		linear-gradient(90deg,  hsla(0, 0%, 100%, 0.9)  0%, hsla(0, 0%, 100%, 0.6) 100%) 100%   0%,
		linear-gradient(180deg, hsla(0, 0%, 100%, 0.6)  0%, hsla(0, 0%, 100%, 0.3) 100%) 100% 100%,
		linear-gradient(360deg, hsla(0, 0%, 100%, 0.3)  0%, hsla(0, 0%, 100%, 0  ) 100%)   0% 100%
	;
	background-repeat: no-repeat;
	background-size: 50% 50%;
	top: -1px;
	bottom: -1px;
	left: -1px;
	right: -1px;
}

.spinner:after {
	background: white;
	border-radius: 50%;
	top: 1%;
	bottom: 1%;
	left: 1%;
	right: 1%;
}

.confirmation {
  display: none;
  text-align: center;
  @media (max-width: 767px) {
    padding: 0 15px;
  }
}

.confirmation-title {
  margin-top: 170px;
  margin-bottom: 70px;
  @media (max-width: 767px) {
    margin-top: 65px;
    margin-bottom: 20px;
  }
}

.confirmation-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 67px;
  width: 67px;
  margin: 0 auto;
  border: 2px solid $secondary-green;
  border-color: transparent;
  border-radius: 50%;
  transform: scale(0.5);
  opacity: 0;
  animation-name: confirmation-icon;
  animation-duration: 1.3s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

@keyframes confirmation-icon {
  0% {
    border-color: transparent;
    opacity: 0;
  }
  30% {
    border-color: transparent;
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
    border-color: $secondary-green;
  }
}

.confirmation-subtitle {
  max-width: 460px;
  margin: 0 auto;
  font-family: $open-sans;
  font-weight: lighter;
  font-size: 20px;
  color: #4A4A4A;
  line-height: 30px;
  margin-top: 70px;
  margin-bottom: 50px;
  @media (max-width: 767px) {
    font-size: 14px;
    max-width: none;
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

.confirmation-cta {
  display: inline-block;
  padding: 12px 20px;;
  color: $primary;
  font-family: $roboto-mono;
  font-size: 20px;
  outline-style: solid;
  outline-width: 1px;
  outline-color: $primary;
  text-decoration: none;
  &:hover {
    outline-style: solid;
    outline-width: 2px;
    outline-color: $primary;
  }
  &:visited {
    color: $primary;
  }
  &:active {
    background-color: #E0E3F7;
  }
}

.confirmation-globe {
  margin: 80px auto 0 auto;
  display: block;
  max-width: 100%;
  height: 250px;
  @media (max-width: 992px) {
    height: auto;
  }
}
