.services-cube {
  position: relative;
  width: 383px;
  height: 383px;
  background-image: url('../images/services-cube.png');
  background-size: contain;
  background-repeat: no-repeat;
  @media (max-width: 992px) {
    // width: 293px;
    // height: 293px;
  }
  @media (max-width: 767px) {
    width: 164px;
    height: 164px;
    margin-bottom: 82px;
    // background-image: url('../images/services-cube-small.png');
    transform: translateY(-60px);
  }
}

.services-cube-label {
  display: inline-block;
  font-size: 20px;
  color: white;
  padding: 5px 10px;
  position: absolute;
  opacity: 0;
  user-select: none;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  &.has-loaded {
    animation-name: basic-opacity;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;
  }
  @media (max-width: 767px) {
    position: relative;
    top: inherit !important;
    bottom: inherit !important;
    left: -1px !important;
    right: inherit !important;
    display: block;
    background-color: transparent !important;
    text-align: center;
    font-weight: lighter;
  }
  @media (max-width: 736px) {
    font-size: 21px;
  }
  @media (max-width: 414px) {
    font-size: 16px;
  }
  &:first-child {
    @media (max-width: 767px) {
      margin-top: 100%;
    }
  }
  &::after {
    content: '';
    display: block;
    width: 85px;
    height: 1px;
    opacity: 0;
    outline: 1px solid transparent;
    @media (max-width: 767px) {
      display: none;
    }
  }
  &.design {
    background-color: #FAC02F;
    top: 0px;
    right: -30%;
    padding: 5px 14px;
    // @media (max-width: 992px) {
    //   top: -26px;
    // }
    @media (max-width: 767px) {
      color: #FAC02F;
    }
    &::after {
      background-color: inherit;
      position: absolute;
      bottom: -6px;
      left: -74px;
      transform: rotate(-30deg);
      animation-name: line-design;
      animation-duration: 1.5s;
      animation-fill-mode: forwards;
      animation-timing-function: ease-in-out;
      animation-delay: 0.5s;
    }
  }
  &.research {
    background-color: $primary;
    bottom: 77px;
    left: -40%;
    // @media (max-width: 992px) {
    //   left: -53%;
    // }
    @media (max-width: 767px) {
      color: $primary;
    }
    &::after {
      width: 60px;
      background-color: inherit;
      position: absolute;
      right: -54px;
      bottom: 15px;
      transform: rotate(-9deg);
      animation-name: line-research;
      animation-duration: 1.5s;
      animation-fill-mode: forwards;
      animation-timing-function: ease-in-out;
      animation-delay: 0.5s;
    }
  }
  &.strategy {
    background-color: #DF3E50;
    right: -45%;
    bottom: 76px;
    // @media (max-width: 992px) {
    //   right: -60%;
    //   bottom: 56px;
    // }
    @media (max-width: 767px) {
      color: #DF3E50;
    }
    &::after {
      background-color: inherit;
      position: absolute;
      bottom: 21px;
      left: -75px;
      transform: rotate(9deg);
      animation-name: line-strategy;
      animation-duration: 1.5s;
      animation-fill-mode: forwards;
      animation-timing-function: ease-in-out;
      animation-delay: 0.5s;
    }
  }
}

.services-section {
  position: relative;
  height: 100vh;
  display: flex;
  font-family: $open-sans;
  font-weight: lighter;
  font-size: 16px;
  color: #1C1D21;
  line-height: 26px;
  max-width: 1024px;
  margin: 0 auto;
  @media (max-width: 992px) {
    height: auto;
    padding: 50px;
  }
  @media (max-width: 767px) {
    font-size: 14px;
    line-height: 24px;
    padding: 0 15px;
    flex-direction: column-reverse;
    margin-bottom: 80px;
  }
  .scroll-prompt {
    bottom: 67px;
  }
  p {
    margin: 0 0 20px 0;
  }
  h2 {
    font-family: 'Roboto Mono', monospace;
    align-self: flex-start;
    margin: 0 0 40px 0;
    @media (max-width: 767px) {
      align-self: center;
      margin: 20px 0;
    }
  }
}

.services-design {
  @media (max-width: 767px) {
    flex-direction: column;
  }
}

.services-section-col {
