    width: 223.5px;
    height: 195.5px;
    @media (max-width: 767px) {
      width: 112px;
      height: 98px;
    }
  }
  .services-icon-design {
    width: 220px;
    height: 220px;
    @media (max-width: 767px) {
      width: 132px;
      height: 131px;
    }
  }
  .services-icon-strategy {
    width: 209.5px;
    height: 200.5px;
    @media (max-width: 767px) {
      width: 146px;
      height: 139px;
    }
  }
  p {
    align-self: flex-start;
    max-width: 100%;
    @media (max-width: 767px) {
      align-self: center;
    }
  }
}

.services-research {
  @media (max-width: 767px) {
    padding-top: 60px;
  }
  .services-section-col:first-child {
    width: 57%;
    padding-left: 120px;
    @media (max-width: 992px) {
      padding-left: 0;
    }
    @media (max-width: 767px) {
      width: 100%;
    }
  }
  .services-section-col:last-of-type {
    align-items: flex-end;
    width: 43%;
    padding-right: 116px;
    @media (max-width: 992px) {
