}

.services-strategy {
  .services-section-col:first-child {
    width: 57%;
    padding-left: 120px;
    @media (max-width: 992px) {
      padding-left: 0;
    }
    @media (max-width: 767px) {
      width: 100%;
    }
  }
  .services-section-col:last-of-type {
    width: 43%;
    @media (max-width: 767px) {
      width: 100%;
    }
