.diagram-slide {
  font-weight: lighter;
  font-family: $open-sans;
  @media (max-width: 767px) {
    margin-bottom: 50px;
    &:last-child {
      margin-bottom: 85px;
    }
  }
  p {
    margin: 0;
    max-width: 425px;
  }
}

.diagram-slide-title {
  display: none;
  @media (max-width: 767px) {
    display: block;
    margin: 0 0 20px 0;
    font-size: 16px;
  }
}

.diagram-slide-number {
  display: none;
  @media (max-width: 767px) {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto 25px auto;
    padding-bottom: 1px;
    font-size: 15px;
    width: 37px;
    height: 37px;
    border-radius: 50%;
    border: 5px solid $secondary-green;
  }
  &::before {
    position: absolute;
    top: -79%;
    left: 0;
    right: 0;
    margin: 0 auto;
    content: '';
    display: block;
    height: 16px;
    width: 1px;
    background-color: white;
  }
}

.diagram {
  position: relative;
  width: 196.27px;
  height: 196.5px;
  background-image: url('../images/services-diagram-placeholder.svg');
  font-size: 16px;
  line-height: 24px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.diagram-number {
  position: relative;
  margin: 0 0 10px 0;
  font-size: 20px;
  &::before, &::after {
    position: relative;
    content: '';
    display: inline-block;
    width: 3px;
    height: 3px;
    border-radius: 50%;
    background-color: #808DDD;
  }
  &::before {
    right: 5px;
    bottom: 5px;
  }
  &::after {
    left: 5px;
    bottom: 5px;
  }
}

.diagram-title {
  margin: 0 0 12px 0;
}

.diagram-nav-point {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 4px solid white;
  position: absolute;
  cursor: pointer;
  transition: all 300ms ease-in-out;
  &:hover, &.is-current {
    background-color: white;
    border-color: $secondary-green;
  }
  &.is-inactive {
    pointer-events: none;
    border-color: #6B7AD8;
  }
}

.diagram-nav-top {
  top: -10px;
  left: 0;
  right: 0;
  margin: 0 auto;
  &::before {
    content: '';
    display: block;
    height: 11px;
    width: 1px;
    background-color: white;
    position: absolute;
    bottom: 144%;
    left: 3px;
  }
  .diagram-dot {
    &:nth-child(1) {
      top: 5px;
      left: 27px;
    }
    &:nth-child(2) {
      top: 15px;
      left: 52px;
    }
    &:nth-child(3) {
      top: 30px;
      left: 73px;
    }
    &:nth-child(4) {
      top: 51px;
      left: 90px;
    }
    &:nth-child(5) {
      top: 75px;
      left: 100px;
    }
  }
}

.diagram-nav-right {
  right: -10px;
  top: 0;
  bottom: 0;
  margin: auto 0;
  .diagram-dot {
    &:nth-child(1) {
      top: 27px;
      left: -2px;
    }
    &:nth-child(2) {
      top: 52px;
      left: -12px;
    }
    &:nth-child(3) {
      top: 73px;
      left: -28px;
    }
    &:nth-child(4) {
      top: 88px;
      left: -49px;
    }
    &:nth-child(5) {
      top: 98px;
      left: -73px;
    }
  }
}

.diagram-nav-bottom {
  right: 0;
  left: 0;
  bottom: -10px;
  margin: 0 auto;
  .diagram-dot {
    &:nth-child(1) {
      top: -3px;
      left: -24px;
    }
    &:nth-child(2) {
      top: -13px;
      left: -48px;
    }
    &:nth-child(3) {
      top: -28px;
      left: -69px;
    }
    &:nth-child(4) {
      top: -49px;
      left: -84px;
    }
    &:nth-child(5) {
      top: -73px;
      left: -95px;
    }
  }
}

.diagram-nav-left {
  left: -10px;
  top: 0;
  bottom: 0;
  margin: auto 0;
  .diagram-dot {
    &:nth-child(1) {
      top: -25px;
      left: 5px;
    }
    &:nth-child(2) {
      bottom: 51px;
      left: 14px;
    }
    &:nth-child(3) {
      bottom: 72px;
      left: 31px;
    }
    &:nth-child(4) {
      bottom: 88px;
      left: 52px;
    }
    &:nth-child(5) {
      bottom: 98px;
      left: 76px;
    }
  }
}

.diagram-carousel {
  font-family: $open-sans;
  font-weight: 600;
  font-size: 16px;
  line-height: 26px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  @media (max-width: 767px) {
    height: auto;
    visibility: visible;
    font-size: 14px;
  }
}

.diagram-dot {
  position: absolute;
  display: block;
  width: 6px;
  height: 6px;
  background-color: white;
  border-radius: 50%;
  &:nth-child(1) {
    opacity: 0;
    transition: opacity 300ms ease-in-out 0.5s;
    &.has-loaded {
      opacity: 1;
      transition: opacity 300ms ease-in-out 0.1s;
    }
  }
  &:nth-child(2) {
    opacity: 0;
    transition: opacity 300ms ease-in-out 0.4s;
    &.has-loaded {
      opacity: 1;
      transition: opacity 300ms ease-in-out 0.2s;
    }
  }
  &:nth-child(3) {
    opacity: 0;
    transition: opacity 300ms ease-in-out 0.3s;
    &.has-loaded {
      opacity: 1;
      transition: opacity 300ms ease-in-out 0.3s;
    }
  }
  &:nth-child(4) {
    opacity: 0;
    transition: opacity 300ms ease-in-out 0.2s;
    &.has-loaded {
      opacity: 1;
      transition: opacity 300ms ease-in-out 0.4s;
    }
  }
  &:nth-child(5) {
    opacity: 0;
    transition: opacity 300ms ease-in-out 0.1s;
    &.has-loaded {
      opacity: 1;
      transition: opacity 300ms ease-in-out 0.5s;
    }
  }
}

.services-colophon {
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @media (max-width: 992px) {
    height: auto;
    display: block;
  }
}

.services-colophon-text {
  font-weight: lighter;
  font-size: 20px;
  color: #2C41C6;
  line-height: 30px;
  margin-bottom: 180px;
  max-width: 700px;
  animation-duration: 2s;
  @media (max-width: 992px) {
    padding: 0 15px;
    margin: 80px auto 0 auto;
  }
  &:first-child {
    margin-bottom: 25px;
    @media (max-width: 767px) {
      margin-bottom: 40px;
    }
  }
  &:last-of-type {
    margin-top: 0;
    margin-bottom: 145px;
    @media (max-width: 767px) {
      margin-bottom: 49px;
    }
  }
}

.services-cta {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid $secondary-red;
  background-color: white;
  font-size: 20px;
  color: $secondary-red;
  width: 180px;
  height: 50px;
  @media (max-width: 992px) {
    margin: 0 auto 100px auto;
    font-size: 18px;
    width: 210px;
    height: 47px;
  }
  &:hover {
    border-width: 2px;
  }
  &:active {
    background-color: #FCE3E4;
  }
}

.services-cta-tooltip {
  position: relative;
  display: inline-block;
  background: $secondary-red;
  color: white;
  font-size: 16px;
  padding: 2px 8px 3px 8px;
  margin-bottom: 10px;
  position: relative;
  z-index: -1;
  animation-duration: 0.5s;
  @media (max-width: 992px) {
    font-size: 12px;
  }
  &::after {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    content: '';
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 8px 5px 0 5px;
    border-color: $secondary-red transparent transparent transparent;
  }
}
