// Shared

@keyframes basic-opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


// Scroll prompt

@keyframes scroll-prompt-circle {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(41px);
  }
}

// Homepage cta

@keyframes cta-arrow-top {
  0% {
    top: 0;
    height: 0;
  }
  100% {
    top: -117px;
    height: 118px;
  }
}

@keyframes cta-arrow-top-horizontal {
  0% {
    left: 0;
    width: 0;
    opacity: 0;
  }
  100% {
    left: -120px;
    width: 120px;
    opacity: 1;
  }
}

@keyframes cta-chevron-top-top {
  0% {
    opacity: 0;
    transform: rotate(0deg);
    top: 0;
    left: 0;
  }
  10% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: rotate(-50deg);
    top: -6px;
    left: -3px;
  }
}

@keyframes cta-chevron-top-bottom {
  0% {
    opacity: 0;
    transform: rotate(0deg);
    bottom: 0;
    left: 0;
  }
  10% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: rotate(50deg);
    bottom: -6px;
    left: -3px;
  }
}

@keyframes cta-arrow-bottom {
  0% {
    height: 0;
  }
  100% {
    height: 118px;
  }
}

@keyframes cta-arrow-bottom-horizontal {
  0% {
    width: 0;
    right: 0;
    opacity: 0;
  }
  100% {
    width: 120px;
    right: -120px;
    opacity: 1;
  }
}

@keyframes cta-chevron-bottom-top {
  0% {
    opacity: 0;
    transform: rotate(0deg);
    top: 0;
    right: 0;
  }
  10% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: rotate(50deg);
    top: -6px;
    right: -3px;
  }
}

@keyframes cta-chevron-bottom-bottom {
  0% {
    opacity: 0;
    transform: rotate(0deg);
    bottom: 0;
    right: 0;
  }
  10% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: rotate(-50deg);
    bottom: -6px;
    right: -3px;
  }
}

