html {
  height: auto !important;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background: #fff;
  font-family: 'Roboto Mono', monospace;
  &.is-fixed {
    position: fixed;
    width: 100%;
  }
  &.cookie-is-open {
    .header {
      margin-top: 100px;
      @media (max-width: 767px) {
        margin-top: 280px;
      }
    }
  }
}

a {
  color: $primary;
  text-decoration: underline;
  &:visited {
    color: #1A237E;
  }
  &:active {
    color: $secondary-green;
  }
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
input[type="number"] {
    -moz-appearance: textfield;
}

input::-webkit-contacts-auto-fill-button {
  visibility: hidden;
  display: none !important;
  pointer-events: none;
  position: absolute;
  right: 0;
}

ul {
  padding: 0;
  margin: 0;
}

// Cookie Notice

.cookie-notice {
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  height: 100px;
  width: 100%;
  background-color: $primary;
  color: white;
  font-family: $open-sans;
  font-weight: lighter;
  font-size: 14px;
  line-height: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 1;
  pointer-events: auto;
  &.is-hidden {
    pointer-events: none;
    opacity: 0;
  }
  padding: 0 43px;
  @media (max-width: 767px) {
    display: block;
    height: 280px;
    padding: 25px 25px 0 25px;
    text-align: center;
  }

  p {
    margin: 0;
    max-width: 510px;
    @media (min-width: 1200px) {
      max-width: 700px;
    }
    @media (max-width: 1100px) {
      max-width: 50%;
    }
    @media (max-width: 992px) {
      width: 50%;
    }
    @media (max-width: 767px) {
      width: 100%;
      max-width: none;
      margin: 0 auto;
    }
  }
  a {
    text-decoration: underline;
  }
}

.cookie-notice-buttons {
  display: inline-block;
  @media (max-width: 767px) {
    margin-top: 20px;
  }
}

.cookie-notice-btn {
  display: inline-block;
  width: 195px;
  height: 45px;
  border: 1px solid white;
  background-color: transparent;
  &:hover {
    border-width: 2px;
  }
  &:focus {
    outline: 0;
  }
  &:active {
    background-color: rgba(255, 255, 255, .2);
  }
  @media (max-width: 992px) {
    width: 142px;
  }
  @media (max-width: 767px) {
    width: 100%;
  }
  &:first-child {
    margin-right: 20px;
    @media (max-width: 767px) {
      margin-right: 0;
      margin-bottom: 13px;
    }
  }
}

// Shared styles

.scroll-prompt {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  bottom: 85px;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 41px;
  width: 1px;
  background-color: $primary;
  &.has-loaded {
    transition: opacity 500ms ease-in-out;
    opacity: 1;
    visibility: visible;
  }
  @media (max-width: 992px) {
    display: none;
  }
  @media (max-height: 650px) {
    display: none;
  }
}

.scroll-prompt-circle {
  position: absolute;
  top: 0;
  left: -3px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: $primary;
  &.has-loaded {
    animation-name: scroll-prompt-circle;
    animation-duration: 2s;
    animation-delay: 0.8s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;
  }
}

.scroll-prompt-text {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  bottom: 50px;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 12px;
  font-weight: lighter;
  &.has-loaded {
    opacity: 1;
    visibility: visible;
    transition: opacity 2000ms ease-in-out;
  }

  @media (max-width: 767px) {
    display: none;
  }
}

// Typography

h1 {
  font-weight: lighter;
  font-size: 44px;
  line-height: 56px;
  color: $primary;
  margin: 0;
}

h2 {
  font-weight: lighter;
  font-size: 24px;
  line-height: 34px;
  color: $primary;
  margin: 0;
}

h3 {
  font-size: 20px;
  font-weight: lighter;
  color: $primary;
  line-height: 30px;
}

// Overrides


#hero-particles,
#services-hero-particles,
#speciality-particles {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
}

#fp-nav {
  span {
    height: 1px !important;
    width: 11px !important;
    margin: 0 !important;
    border-radius: 0 !important;
    transition: background-color 300ms ease-in-out;
    &.is-grey {
      background-color: #979797;
    }
    &.is-white {
      background-color: #FFFFFF;
    }
  }
}

#fp-nav a.active span {
  width: 21px !important;
}

#fp-nav a span:hover {
  width: 21px !important;
  transition: width 300ms ease-in-out;
}

.slick-slide, .slick-slide * {
  outline: none !important;
}

.slick-dots {
  text-align: left;
  left: -10px;
  bottom: -44px;
}

.slick-dots li button:before {
  color: white;
}

.slick-dots li.slick-active button:before {
  color: white;
  opacity: 1;
}
