body.cookie-is-open {
  @media (max-height: 800px) and (min-width: 767px) {
    .speciality-circle {
      width: 350px;
      height: 350px;
    }
    .speciality-title {
      font-size: 45px;
    }
  }
  @media (max-height: 680px) and (min-width: 767px) {
    .speciality-circle {
      width: 254px !important;
      height: 254px !important;
      margin-top: 20vh;
    }
    .speciality-title {
      font-size: 32px !important;
    }
    .speciality-subtitle {
      font-size: 14px !important;
    }
  }
}

.speciality-content-wrapper {
  animation-duration: 1.5s;
}

.speciality-hero {
  position: relative;
  height: 100vh;
  background-image: linear-gradient(to top, #E5A0A5 5%, #FBE7E8 34%, #FFFFFF 98%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow: hidden;
  .scroll-prompt-circle {
    animation-delay: 1s;
  }
  @media (max-width: 767px) {
    margin-top: -65px;
    .speciality-circle {
      position: relative;
      bottom: 50px;
    }
    .scroll-prompt {
      bottom: 115px;
    }
    .scroll-prompt-text {
      bottom: 80px;
    }
  }
  @media (max-width: 736px) and (max-height: 414px) {
    min-height: 500px;
    .scroll-prompt {
      display: none;
    }
    .scroll-prompt-text {
      display: none;
    }
    .speciality-circle {
      position: static;
    }
  }
  @media (max-width: 992px) {
    .scroll-prompt {
      display: block;
    }
    .scroll-prompt-text {
      display: block;
    }
  }
}

.speciality-circle {
  width: 400px;
  height: 400px;
  background-color: $secondary-red;
  color: white;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
  opacity: 0;
  transform: translateY(120%);
  animation-name: rising-sun;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  @media (max-width: 767px) {
    width: 254px;
    height: 254px;
  }
}

@keyframes rising-sun {
  0% {
    opacity: 0;
    transform: translateY(150%);
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: translate(0%);
  }
}

.speciality-title {
  user-select: none;
  margin: 0;
  font-size: 52px;
  opacity: 0;
  line-height: inherit;
  color: white;
  font-weight: 300;
  animation-name: basic-opacity;
  animation-fill-mode: forwards;
  animation-duration: 1s;
  animation-delay: 1.5s;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  @media (max-width: 767px) {
    font-size: 32px;
  }
}

.speciality-subtitle {
  user-select: none;
  font-size: 16px;
  margin: 0;
  opacity: 0;
  animation-name: basic-opacity;
  animation-fill-mode: forwards;
  animation-duration: 1s;
  animation-delay: 2s;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  @media (max-width: 767px) {
    font-size: 14px;
  }
}

.speciality-section {
  max-width: 1024px;
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: $open-sans;
  font-weight: lighter;
  font-size: 16px;
  line-height: 26px;
  padding: 0 200px;
  height: 100vh;
  @media (max-width: 992px) {
    height: auto;
    padding: 50px;
    font-size: 14px;
    line-height: 24px;
  }
  @media (max-width: 767px) {
    padding: 80px 15px 0 15px;
    text-align: center;
  }

  &.has-columns {
    flex-direction: row;
    align-items: center;
    @media (max-width: 767px) {
      flex-direction: column-reverse;
    }
  }
  .image-column {
    width: 50%;
    img {
      height: 350px;
      max-width: 100%;
    }
    @media (max-width: 767px) {
      width: 100%;
      margin-top: 30px;
    }
  }
  .content-column {
    width: 50%;
    @media (max-width: 767px) {
      width: 100%;
    }
  }
  h2 {
    margin-bottom: 40px;
    color: $secondary-red;
    font-family: 'Roboto Mono', monospace;
    font-weight: lighter;
    @media (max-width: 992px) {
      margin-bottom: 20px;
    }
  }
  p {
    margin: 0 0 20px 0;
  }
  img {
    max-width: 100%;
    height: auto;
    align-self: left;
  }
  .scroll-prompt {
    bottom: 67px;
  }
  &.padding-variant {
    @media (max-width: 767px) {
      padding-top: 50px;
    }
  }
  .desktop-img {
    @media (max-width: 767px) {
      display: none;
    }
  }
  .mobile-img {
    display: none;
    @media (max-width: 767px) {
      display: block;
      max-width: 248px;
      margin: 0 auto;
    }
  }
}

.speciality-section-1 {
  blockquote {
    display: block;
    font-family: 'Roboto Mono', monospace;
    font-size: 20px;
    color: $primary;
    line-height: 30px;
    margin: 50px 0;
    padding-left: 80px;
    @media (max-width: 767px) {
      padding-left: 0;
    }
  }
}

.speciality-section-2 {
  img {
    max-width: 520px;
  }
  blockquote {
    display: block;
    font-family: 'Roboto Mono', monospace;
    font-size: 20px;
    color: $primary;
    line-height: 30px;
    margin: 50px 0 30px 0;
    padding-left: 80px;
    @media (max-width: 767px) {
      padding-left: 0;
      margin: 30px 0;
    }
  }
}

.speciality-section-3 {
  blockquote {
    display: block;
    font-family: 'Roboto Mono', monospace;
    font-size: 20px;
    color: $primary;
    line-height: 30px;
    margin: 0 0 40px 0;
    @media (max-width: 767px) {
      margin-bottom: 20px;
    }
  }
}

.speciality-section-4 {
  blockquote {
    display: block;
    font-family: 'Roboto Mono', monospace;
    font-size: 20px;
    color: $primary;
    line-height: 30px;
    margin-left: 0;
    margin-right: 0;
    padding-left: 80px;
    @media (max-width: 767px) {
      padding-left: 0;
    }
    &:first-of-type {
      margin-top: 50px;
      margin-bottom: 0;
      @media (max-width: 767px) {
        margin-bottom: 30px;
      }
    }
    &:last-of-type {
      margin-top: 0;
      margin-bottom: 50px;
    }
  }
}

.speciality-section-5 {
  blockquote {
    display: block;
    font-family: 'Roboto Mono', monospace;
    font-size: 20px;
    color: $primary;
    line-height: 30px;
    padding-left: 80px;
    margin: 50px 0 0 0;
    @media (max-width: 767px) {
      padding-left: 0;
      margin-top: 80px;
    }
  }
}

.sprint-icons {
  display: flex;
  margin-top: 50px;
  @media (max-width: 767px) {
    flex-direction: column;
  }
}

.sprint-icon {
  position: relative;
  display: inline-block;
  height: 94px;
  width: 95px;
  background-repeat: no-repeat;
  background-size: contain;
  animation-name: basic-opacity;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  opacity: 0;
  flex-shrink: 0;
  @media (max-width: 767px) {
    animation-name: none;
    opacity: 1;
  }
  &.sprint-icon-1 {
    background-image: url('../images/sprint-icon-1.svg');
  }
  &.sprint-icon-2 {
    background-image: url('../images/sprint-icon-2.svg');
    animation-delay: 0.5s;
    .sprint-arrow  {
      animation-delay: 0.5s;
    }
  }
  &.sprint-icon-3 {
    background-image: url('../images/sprint-icon-3.svg');
    animation-delay: 1s;
    .sprint-arrow  {
      animation-delay: 1s;
    }
  }
  &.sprint-icon-4 {
    background-image: url('../images/sprint-icon-4.svg');
    animation-delay: 1.5s;
    .sprint-arrow  {
      animation-delay: 1.5s;
    }
  }
  &.sprint-icon-5 {
    background-image: url('../images/sprint-icon-5.svg');
    animation-delay: 2s;
    &.sprint-arrow  {
      display: none;
    }
  }
}

.sprint-arrow {
  position: absolute;
  top: 42px;
  right: -30px;
  background-color: white;
  content: '';
  display: block;
  width: 35px;
  height: 10px;
  background-image: url('../images/sprint-arrow.svg');
  background-repeat: no-repeat;
  transform: rotate(85deg);
  transform-origin: left;
  animation-name: sprint-arrow;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  @media (max-width: 767px) {
    top: inherit;
    right: 1px;
    left: 0;
    bottom: -13px;
    margin: 0 auto;
    width: 9px;
    height: 16px;
    background-image: url('../images/sprint-arrow-2.svg');
    animation-name: none;
    transform: rotate(0);
  }
}

.sprint-icon-container {
  text-align: center;
  @media (max-width: 767px) {
    max-width: 191px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 18px;
    margin-left: auto;
    margin-right: auto;
    &:last-of-type {
      margin-bottom: 0;
    }
  }
}

.sprint-icon-container + .sprint-icon-container {
  margin-left: 39px;
  @media (max-width: 767px) {
    margin-left: auto;
    margin-right: auto;
  }
}

@keyframes sprint-arrow {
  0% {
    transform: rotate(85deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.sprint-day {
  opacity: 0;
  display: block;
  line-height: 1;
  text-transform: uppercase;
  font-size: 10px;
  color: #4A4A4A;
  margin-bottom: 15px;
  letter-spacing: 1.2px;
  animation-name: basic-opacity;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  @media (max-width: 767px) {
    display: none;
    margin: 0;
  }
  &.sprint-day-2 {
    animation-delay: 0.5s;
  }
  &.sprint-day-3 {
    animation-delay: 1s;
  }
  &.sprint-day-4 {
    animation-delay: 1.5s;
  }
  &.sprint-day-5 {
    animation-delay: 2s;
  }
}

.sprint-step {
  opacity: 0;
  display: block;
  line-height: 1;
  font-size: 12px;
  color: $secondary-red;
  margin-top: 15px;
  animation-name: basic-opacity;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  @media (max-width: 767px) {
    display: none;
    margin: 0;
  }
  &.sprint-step-2 {
    animation-delay: 0.5s;
  }
  &.sprint-step-3 {
    animation-delay: 1s;
  }
  &.sprint-step-4 {
    animation-delay: 1.5s;
  }
  &.sprint-step-5 {
    animation-delay: 2s;
  }
}

.sprint-info {
  display: none;
  @media (max-width: 767px) {
    display: block;
    margin-left: 14px;
    text-align: left;
    min-width: 82px;
    .sprint-day {
      display: block;
      margin-bottom: 5px;
    }
    .sprint-step {
      display: block;
    }
  }
}

.speciality-cta {
  align-items: center;
  @media (max-width: 992px) {
    height: 600px;
    overflow: hidden;
  }
  @media (max-width: 767px) {
    height: auto;
    padding: 0;
  }
}

.speciality-cta-img {
  position: relative;
  height: 220px;
  width: 220px;
  background-image: url('../images/speciality-cta.svg');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  bottom: 80px;
  margin-bottom: 80px;
  @media (max-width: 992px) {
    bottom: 140px;
  }
  @media (max-width: 767px) {
    display: none;
  }
}

.speciality-arrow {
  position: absolute;
  display: block;
  width: 112px;
  height: 1px;
}

.speciality-arrow-left {
  top: 102px;
  left: -53%;
  background-color: $secondary-red;
  animation-name: speciality-arrow-left;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@keyframes speciality-arrow-left {
  0% {
    left: 0;
    width: 0;
  }
  100% {
    left: -53%;
    width: 112px;
  }
}

.speciality-arrow-right {
  top: 102px;
  right: -53%;
  background-color: $primary;
  animation-name: speciality-arrow-right;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

@keyframes speciality-arrow-right {
  0% {
    right: 0;
    width: 0;
  }
  100% {
    right: -53%;
    width: 112px;
  }
}

.speciality-arrow-vertical {
  position: absolute;
  height: 127px;
  width: 1px;
  animation-name: speciality-arrow-vertical;
  animation-duration: 1.3s;
  animation-fill-mode: forwards;
  animation-delay: 0.7s;
  opacity: 0;
}

.speciality-arrow-dot {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: inherit;
  opacity: 0;
  animation-name: speciality-dot;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-delay: 1.5s;
}

@keyframes speciality-arrow-vertical {
  0% {
    opacity: 0;
    height: 0;
  }
  100% {
    opacity: 1;
    height: 127px;
  }
}

@keyframes speciality-dot {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
    // transform: scale(2);
  }
  100% {
    opacity: 1;
    // transform: scale(1);
  }
}

.speciality-arrow-vertical-left {
  top: 0;
  left: 0;
  background-color: inherit;
  .speciality-arrow-dot {
    bottom: -5px;
    left: -2px;
  }
}

.speciality-arrow-vertical-right {
  top: 0;
  right: 0;
  background-color: inherit;
  .speciality-arrow-dot {
    bottom: -5px;
    left: -2px;
  }
}

.speciality-cta-content {
  position: absolute;
  top: 100%;
  left: -166px;
  min-width: 342px;
  text-align: center;
  margin-top: 25px;
  opacity: 0;
  animation-name: basic-opacity;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-delay: 2s;
  @media (max-width: 992px) {
    left: -140px;
    min-width: 272px;
  }
  p {
    min-height: 99px;
  }
}

.speciality-cta-btn {
  margin-top: 50px;
  width: 166px;
  height: 50px;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px auto 0 auto;
  border: 1px solid black;
  font-size: 20px;
  text-decoration: none;
  &:hover {
    border-width: 2px;
  }
}

.speciality-cta-btn-left {
  color: $secondary-red;
  border-color: $secondary-red;
  &:visited {
    color: $secondary-red;
  }
  &:active {
    background-color: #FCE3E4;
  }
}

.speciality-cta-btn-right {
  color: $primary;
  border-color: $primary;
  &:visited {
    color: $primary;
  }
  &:active {
    background-color: #E0E3F7;
  }
}

.speciality-cta-mobile {
  margin-top: 80px;
  display: none !important;
  @media (max-width: 767px) {
    display: block !important;
  }
}

.speciality-cta-box {
  padding-top: 30px;
  background-color: $secondary-red;
  &:last-of-type {
    background-color: $primary;
  }
}
