body.cookie-is-open {
  .error-page-container {
    transform: translateY(100px);
    margin-bottom: 100px;
  }
}

.error-page-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @media (min-height: 1016px) {
    height: 87vh;
  }
}

.error-page-title {
  margin-top: 130px;
  margin-bottom: 40px;
  text-align: center;
  @media (max-width: 767px) {
    margin-top: 48px;
    margin-bottom: 30px;
    padding: 0 15px;
  }
  &.variant {
    margin-top: 15px;
    margin-bottom: 40px;
    @media (max-width: 767px) {
      margin-top: 40px;
    }
  }
}

.venn-circle {
  width: 192px;
  height: 192px;
  border: 1px solid $primary;
  color: $primary;
  font-weight: lighter;
  font-family: $roboto-mono;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: top;
  font-size: 16px;
  text-align: center;
  @media (max-width: 767px) {
    width: 150px;
    height: 150px;
    font-size: 14px;
  }
}

.venn-diagram {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.bottom-circles {
  display: flex;
  position: relative;
  bottom: 55px;
  @media (max-width: 767px) {
    bottom: 39px;
  }
}

.venn-1 {
  p {
    position: relative;
    left: 1px;
  }
}

.venn-2 {
  position: relative;
  left: 15px;
  @media (max-width: 767px) {
    left: 10px;
  }
}

.venn-3 {
  position: relative;
  right: 15px;
  @media (max-width: 767px) {
    right: 10px;
  }
  p {
    position: relative;
    left: 13px;
    @media (max-width: 767px) {
      left: 7px;
    }
  }
}

.venn-subtitle {
  margin: 0;
  font-weight: lighter;
  font-style: italic;
  font-size: 12px;
  color: #4A4A4A;
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  text-align: center;
  @media (max-width: 767px) {
    bottom: 0px;
  }
}

.error-page-buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 100px;
  @media (max-width: 992px) {
    flex-direction: column;
    align-items: center;
  }
}

.error-page-btn {
  display: inline-block;
  width: 233px;
  height: 50px;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: lighter;
  font-family: $roboto-mono;
  animation-name: basic-opacity;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  opacity: 0;
  text-decoration: none;
  @media (max-width: 992px) {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  @media (max-width: 767px) {
    width: 210px;
  }
  &:hover {
    border-width: 2px;
  }
  &:first-child {
    border-color: $primary;
    color: $primary;
  }
  &:first-child:active {
    background-color: #E0E3F7;
  }
  &:nth-child(2) {
    border-color: $secondary-red;
    color: $secondary-red;
    animation-delay: 0.5s;
  }
  &:nth-child(2):active {
    background-color: #FCE3E4;
  }
  &:last-of-type {
    border-color: $secondary-green;
    color: $secondary-green;
    animation-delay: 1s;
  }
  &:last-of-type:active {
    background-color: #E1F8F0;
  }
}

.error-page-btn + .error-page-btn {
  margin-left: 45px;
  @media (max-width: 992px) {
    margin-left: 0;
  }
}
