.hero {
  position: relative;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to top, #f0f2fe, #f6f8ff 31%, #ffffff);
  overflow: hidden;
  @media (max-width: 767px) {
    margin-top: -65px;
  }
  .scroll-prompt {
    @media (max-width: 992px) {
      display: block;
    }
    @media (max-width: 767px) {
      bottom: 115px;
    }
  }
  .scroll-prompt-text {
    @media (max-width: 767px) {
      display: block;
      bottom: 80px;
    }
  }
  @media (max-height: 414px) {
    .scroll-prompt {
      bottom: 60px;
    }
    .scroll-prompt-text {
      bottom: 20px;
    }
  }
}

.hero-title {
  max-width: 612px;
  text-align: center;
  visibility: hidden;
  user-select: none;
  animation-duration: 2s;
  @media (max-width: 767px) {
    font-size: 24px;
    line-height: 34px;
    padding: 0 15px;
  }
}

.overview-wrapper {
  animation-duration: 1.5s;
}

.overview {
  position: relative;
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @media (max-width: 767px) {
    height: auto;
    padding: 0 15px;
  }

  p {
    color: #1C1D21;
    max-width: 620px;
    font-family: $open-sans;
    font-weight: lighter;
    font-size: 22px;
    line-height: 32px;
    margin: 0;
    @media (max-width: 767px) {
      line-height: 24px;
      font-size: 14px;
      margin-top: 100px;
    }
  }
  .scroll-prompt {
    bottom: 67px;
  }
}

.overview-highlight {
  font-weight: lighter;
  color: $primary;
  max-width: 620px;
  font-size: 34px;
  line-height: 50px;
  margin: 70px 0 0 0;
  @media (max-width: 767px) {
    font-size: 20px;
    margin-top: 50px;
    margin-bottom: 120px;
    line-height: 30px;
  }
}

.cta {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: 992px) {
    display: block;
    height: auto;
  }
}

.cta-center-point {
  position: relative;
  display: block;
  border-radius: 50%;
  width: 9px;
  height: 9px;
  background-color: $secondary-green;
  @media (max-width: 992px) {
    display: none;
  }
}

.cta-arrow-top {
  position: absolute;
  top: -117px;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: block;
  height: 118px;
  width: 1px;
  background-color: $secondary-green;
  animation-name: cta-arrow-top;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

.cta-arrow-top-horizontal {
  opacity: 0;
  position: absolute;
  top: 0;
  left: -120px;
  width: 120px;
  height: 1px;
  background-color: $secondary-green;
  animation-name: cta-arrow-top-horizontal;
  animation-duration: 1.5s;
  animation-delay: 0.4s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  &::after, &::before {
    opacity: 0;
    content: '';
    display: block;
    position: absolute;
    background-color: $secondary-green;
    width: 16px;
    height: 1px;
  }
  &::after {
    transform: rotate(-50deg);
    top: -6px;
    left: -3px;
    animation-name: cta-chevron-top-top;
    animation-duration: 0.5s;
    animation-delay: 1.5s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;
  }
  &::before {
    transform: rotate(50deg);
    bottom: -6px;
    left: -3px;
    animation-name: cta-chevron-top-bottom;
    animation-duration: 0.5s;
    animation-delay: 1.5s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;
  }
}

.cta-arrow-bottom {
  position: absolute;
  top: 9px;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: block;
  height: 118px;
  width: 1px;
  background-color: $primary;
  animation-name: cta-arrow-bottom;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

.cta-arrow-bottom-horizontal {
  opacity: 0;
  position: absolute;
  bottom: 0;
  right: -120px;
  width: 120px;
  height: 1px;
  background-color: $primary;
  animation-name: cta-arrow-bottom-horizontal;
  animation-duration: 1.5s;
  animation-delay: 0.4s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  &::after, &::before {
    content: '';
    display: block;
    position: absolute;
    background-color: $primary;
    width: 16px;
    height: 1px;
  }
  &::after {
    opacity: 0;
    transform: rotate(50deg);
    top: -6px;
    right: -3px;
    animation-name: cta-chevron-bottom-top;
    animation-duration: 0.5s;
    animation-delay: 1.8s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;
  }
  &::before {
    opacity: 0;
    transform: rotate(-50deg);
    bottom: -6px;
    right: -3px;
    animation-name: cta-chevron-bottom-bottom;
    animation-duration: 0.5s;
    animation-delay: 1.8s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;
  }
}

.cta-button {
  opacity: 0;
  position: absolute;
  width: 234px;
  height: 50px;
  border: 1px solid $secondary-green;
  color: $secondary-green;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: lighter;
  animation-name: basic-opacity;
  animation-duration: 1s;
  animation-delay: 1.6s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  text-decoration: none;
  &:visited {
    color: $secondary-green;
  }
  @media (max-width: 767px) {
    opacity: 1;
    animation-name: none;
  }
  &:hover {
    border-width: 2px;
  }
  &:active {
    background-color: #E1F8F0;
    @media (max-width: 767px) {
      background-color: rgba(255, 255, 255, 0.2);
    }
  }
}

.cta-button-top {
  top: -23px;
  right: 105%;
}

.cta-button-bottom {
  top: -23px;
  left: 105%;
  border-color: $primary;
  color: $primary;
  &:active {
    background-color: #E0E3F7;
  }
  &:visited {
    color: $primary;
  }
}

.cta-content {
  opacity: 0;
  position: absolute;
  min-width: 300px;
  text-align: center;
  font-size: 16px;
  color: #1C1D21;
  line-height: 26px;
  font-family: $open-sans;
  font-weight: lighter;
  animation-name: basic-opacity;
  animation-duration: 1s;
  animation-delay: 1.7s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  p {
    margin: 0;
  }
}

.cta-content-left {
  top: 75px;
  right: 91px;
  img {
    margin-top: 40px;
  }
}

.cta-content-right {
  bottom: 90px;
  left: 88px;
  img {
    margin-bottom: 40px;
  }
}

.cta-mobile {
  display: none;
  @media (max-width: 992px) {
    display: block;
  }
}

.cta-mobile-box {
  background-color: $secondary-green;
  color: white;
  text-align: center;
  font-family: $open-sans;
  font-weight: lighter;
  line-height: 26px;
  padding: 0 42px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  img {
    margin-top: 30px;
    margin-bottom: 20px;
  }
  p {
    margin: 0;
  }
  .cta-button {
    position: static;
    border-color: white;
    color: white;
    margin-top: 30px;
    margin-bottom: 50px;
    font-size: 18px;
    width: 210px;
  }
  &:last-child {
    background-color: $primary;
  }
}
