body.cookie-is-open {
  .terms-page-container {
    transform: translateY(100px);
    margin-bottom: 200px;
    @media (max-width: 767px) {
      transform: translateY(0);
      margin-bottom: 0;
    }
  }
}

.terms-title {
  margin-top: 125px;
  margin-bottom: 40px;
  text-align: center;
  @media (max-width: 767px) {
    margin-top: 65px;
  }
}

.terms-heading {
  font-family: $roboto-mono;
  font-size: 16px;
  color: $primary;
  line-height: 26px;
  font-weight: lighter;
  margin: 0;
  @media (max-width: 767px) {
    font-size: 14px;
    line-height: 24px;
  }
}

.terms {
  margin: 0 auto;
  max-width: 621px;
  font-family: $open-sans;
  font-size: 16px;
  font-weight: lighter;
  color: #1C1D21;
  line-height: 26px;
  p {
    margin-top: 0;
    margin-bottom: 30px;
    &.terms-list-lead {
      margin-bottom: 0;
    }
  }
  @media (max-width: 767px) {
    margin: 0;
    padding: 0 15px;
    font-size: 14px;
    line-height: 24px;
  }
}

.terms-point {
  margin-bottom: 50px;
  @media (max-width: 767px) {
    margin-bottom: 40px;
  }
}

.terms-list {
  margin: 0;
  padding-left: 55px;
  li {
    position: relative;
    list-style: none;
    &::before {
      position: absolute;
      top: 0;
      left: -24px;
      content: '';
      color: $primary;
      display: inline-block;
    }
  }
}

.terms-address {
  font-family: $open-sans;
  margin-bottom: 100px;
  font-weight: lighter;
  font-size: 16px;
  @media (max-width: 767px) {
    font-size: 14px;
    line-height: 24px;
  }
  h5 {
    font-size: 16px;
    font-weight: 600;
    margin: 0;
  }
  p {
    margin: 0;
  }
}
