.header {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  right: 0;
  padding: 0 40px;
  height: 65px;
  color: #000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  @media (max-width: 767px) {
    padding: 0 20px;
    position: relative;
  }
}

.header-nav {
  position: relative;
  top: 8px;
  padding: 0;
  margin: 0;
  font-weight: lighter;
  @media (max-width: 992px) {
    position: fixed;
    z-index: 2;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    text-align: center;
    color: white;
    overflow: scroll;
    opacity: 0;
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    &.is-open {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.header-nav-item {
  position: relative;
  list-style: none;
  display: inline-block;
  font-size: 15px;
  color: black;
  a {
    text-decoration: none;
    &:visited {
      color: black;
    }
  }
  @media (max-width: 992px) {
    display: table;
    margin: 0 auto 7% auto;
    font-size: 16px;
  }
  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    bottom: -2px;
    left: 0;
    margin-top: 2px;
    background-color: #000;
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out;
    @media (max-width: 992px) {
      background-color: white;
      bottom: -3px;
    }
  }
  &:hover, &.is-active {
    &::after {
      visibility: visible;
      transform: scaleX(1);
    }
  }
}

.header-nav-item + .header-nav-item {
  margin-left: 40px;
  @media (max-width: 992px) {
    margin: 0 auto 7% auto;
  }
}

.menu-btn {
  display: none;
  position: relative;
  z-index: 3;
  width: 22px;
  height: 20px;
  border: 0;
  background-color: transparent;
  background-image: url('../images/menu-btn.svg');
  background-size: contain;
  background-repeat: no-repeat;
  outline: 0;
  @media (max-width: 992px) {
    display: block;
  }
   &.is-open {
    background-image: url('../images/menu-btn-close.svg');
  }
}

.menu-circle {
  border-radius: 50%;
  width: 0;
  height: 0;
	position:absolute;
  top: 0;
  right: 0;
	background-color: $primary;
	opacity: 1;
  transition: all 400ms cubic-bezier(0.000, 0.995, 0.990, 1.000);
  @media (min-width: 767px) {
    transition: all 1500ms cubic-bezier(0.000, 0.995, 0.990, 1.000);
  }
  &.is-open {
    width: 1500px;
    height: 1500px;
    top: -560px;
    right: -565px;
    transition: all 900ms cubic-bezier(0.000, 0.995, 0.990, 1.000);
    position: fixed;
    @media (min-width: 767px) {
      width: 3200px;
      height: 3200px;
      transition: all 4000ms cubic-bezier(0.000, 0.995, 0.990, 1.000);
    }
    @media (min-width: 992px) {
      display: none;
    }
  }
}
