.footer {
  font-family: 'Open Sans', sans-serif;
  font-weight: lighter;
  font-size: 14px;
  height: 200px !important;
  background-color: black;
  color: #9B9B9B;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  p {
    margin: 0;
    line-height: 24px;
    &:first-of-type {
      position: relative;
      padding-left: 48px;
      &::after {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 28px;
        height: 49px;
        background-image: url('../images/footer-privacy.svg');
      }
    }
    &:last-child {
      margin-top: 25px;
      color: #4A4A4A;
      a {
        color: inherit;
        text-decoration: none;
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
  a {
    color: $secondary-green;
    text-decoration: underline;
  }
}

.footer-social {
  padding: 0;
  margin: 0 0 20px 0;
  li {
    display: inline-block;
  }

  li + li {
    margin-left: 8px
  }
}

.footer-social-icon {
  display: inline-block;
  height: 25px;
  width: 26px;
  text-indent: -9999px;
  background-repeat: no-repeat;
  &.twitter {
    background-image: url('../images/social-twitter.svg');
  }
  &.facebook {
    background-image: url('../images/social-facebook.svg');
  }
  &.instagram {
    background-image: url('../images/social-instagram.svg');
  }
  &.linkedin {
    background-image: url('../images/social-linkedin.svg');
  }
}
