body.cookie-is-open {

  @media (min-width: 767px) {
    .clients-globe {
      transform: translateY(46px);
      width: 350px;
      height: 350px;
    }
    .clients-dot-description::after {
      top: 100%;
    }
  }
}

.clients-wrapper {
  #fp-nav {
    display: none !important;
  }
}

.clients-hero {
  position: relative;
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(to top, #f0f2fe, #f6f8ff 31%, #ffffff);
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: 736px) {
    height: 488px;
  }
  @media (max-width: 414px) {
    height: 100vh;
    margin-top: -65px;
  }
  @media (max-height: 650px) and (min-width: 767px) {
    .scroll-prompt, .scroll-prompt-text {
      display: none !important;
    }
  }
  .scroll-prompt, .scroll-prompt-text {
    display: block !important;
  }
  .scroll-prompt {
    @media (max-height: 767px) and (min-width: 992px) {
      bottom: 50px;
    }
    @media (max-width: 767px) {
      bottom: 115px;
    }
  }
  .scroll-prompt-text {
    @media (max-height: 767px) and (min-width: 992px) {
      bottom: 20px;
    }
    @media (max-width: 767px) {
      display: block;
      bottom: 80px;
    }
  }
}

.clients-globe {
  position: relative;
  width: 440px;
  height: 441px;
  background-image: url('../images/clients-globe.png');
  background-size: contain;
  background-repeat: no-repeat;
  opacity: 0;
  &.has-loaded {
    animation-name: basic-opacity;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in;
  }
  @media (max-width: 767px) {
    width: 243px;
    height: 241px;
    bottom: 45px;
  }
}

.clients-dot {
  display: block;
  position: absolute;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background-color: $secondary-red;
  opacity: 0;
  &.has-loaded {
    animation-name: clients-dot;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in;
  }
}

@keyframes clients-dot {
  0% {
    opacity: 0;
    transform: scale(2.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.pulse {
  position: absolute;
  border: 10px solid $secondary-red;
  background: transparent;
  border-radius: 60px;
  height: 50px;
  width: 50px;
  animation-name: pulse;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
  top: -21px;
  left: -21px;
  z-index: 1;
  opacity: 0;
}

.dot-technology {
  width: 8px;
  height: 8px;
  top: 38px;
  left: 89px;
  animation-delay: 0.5s;
  @media (max-width: 767px) {
    height: 6px;
    width: 6px;
    top: 22px;
    left: 45px;
    .pulse {
      top: -22px;
      left: -22px;
    }
  }
}

.dot-telecomunications {
  top: 100px;
  left: 232px;
  width: 16px;
  height: 16px;
  background-color: $secondary-green;
  user-select: none;
  @media (max-width: 767px) {
    top: 53px;
    left: 124px;
    width: 13px;
    height: 13px;
  }
  .pulse {
    top: -17px;
    left: -17px;
    border-color: $secondary-green;
    @media (max-width: 767px) {
      top: -19px;
      left: -18px
    }
  }
}

.dot-financial {
  width: 12px;
  height: 12px;
  left: inherit;
  top: 188px;
  right: 21px;
  background-color: $primary;
  @media (max-width: 767px) {
    top: 103px;
    right: 26px;
    width: 9px;
    height: 9px;
  }
  .pulse {
    top: -19px;
    left: -19px;
    border-color: $primary;
    @media (max-width: 767px) {
      top: -21px;
      left: -20px;
    }
  }
}

.dot-retail {
  top: inherit;
  bottom: 189px;
  left: 126px;
  width: 11px;
  height: 11px;
  background-color: $secondary-purple;
  animation-delay: 0.8s;
  @media (max-width: 767px) {
    bottom: 103px;
    left: 64px;
    width: 9px;
    height: 9px;
  }
  .pulse {
    top: -20px;
    left: -20px;
    border-color: $secondary-purple;
    @media (max-width: 767px) {
      top: -21px;
      left: -20px;
    }
  }
}

.dot-goverment {
  top: inherit;
  bottom: 67px;
  left: 54px;
  width: 8px;
  height: 8px;
  background-color: #FA7822;
  animation-delay: 0.4s;
  @media (max-width: 767px) {
    bottom: 35px;
    left: 28px;
    width: 6px;
    height: 6px;
  }
  .pulse {
    top: -21px;
    left: -21px;
    border-color: #FA7822;
  }
}

.dot-devices {
  top: inherit;
  left: inherit;
  right: 41px;
  bottom: 88px;
  width: 9px;
  height: 9px;
  background-color: $secondary-yellow;
  animation-delay: 0.8s;
  @media (max-width: 767px) {
    bottom: 21px;
    right: 50px;
    width: 9px;
    height: 9px;
  }
  .pulse {
    top: -19px;
    left: -19px;
    border-color: $secondary-yellow;
  }
}

.clients-dot-description {
  color: white;
  white-space: nowrap;
  position: absolute;
  top: -30px;
  left: -82px;
  padding: 1px 6px;
  font-size: 14px;
  opacity: 0;
  user-select: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 600;
  @-moz-document url-prefix() {
    left: -86px;
  }
  &.has-loaded {
    animation-name: basic-opacity;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in;
  }
  @media (max-width: 767px) {
    display: none;
  }
  &::after {
    content: '';
    display: block;
    width: 0;
    height: 8px;
    border-style: solid;
    border-width: 6px 6px 0 6px;
    margin: 0 auto;
    position: absolute;
    top: 20px;
    left: 0;
    right: 0;
  }
  &.description-technology {
    background-color: $secondary-red;
    &::after {
      border-color: $secondary-red transparent transparent transparent;
    }
  }
  &.description-telecomunications {
    background-color: $secondary-green;
    left: -74px;
    @-moz-document url-prefix() {
      left: -79px;
    }
    &::after {
      border-color: $secondary-green transparent transparent transparent;
    }
  }
  &.description-financial {
    background-color: $primary;
    left: -76px;
    @-moz-document url-prefix() {
      left: -80px;
    }
    &::after {
      border-color: $primary transparent transparent transparent;
    }
  }
  &.description-retail {
    background-color: $secondary-purple;
    left: -26px;
    @-moz-document url-prefix() {
      left: -28px;
    }
    &::after {
      border-color: $secondary-purple transparent transparent transparent;
    }
  }
  &.description-goverment {
    background-color: #FA7822;
    left: -44px;
    @-moz-document url-prefix() {
      left: -46px;
    }
    &::after {
      border-color: #FA7822 transparent transparent transparent;
    }
  }
  &.description-devices {
    background-color: $secondary-yellow-2;
    left: -73px;
    @-moz-document url-prefix() {
      left: -76px;
    }
    &::after {
      border-color: $secondary-yellow-2 transparent transparent transparent;
    }
  }
}

@keyframes pulse {
  0% {
    transform: scale(0.1, 0.1); opacity: 0.0;
  }
  25% {
    opacity: 0.5
  };
  100% {
    transform: scale(1, 1); opacity: 0.0;
  }
}

.clients-section {
  position: relative;
  text-align: center;
  font-family: $open-sans;
  font-weight: lighter;
  font-size: 16px;
  line-height: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  color: #1C1D21;
  h2 {
    margin-bottom: 40px;
  }
  p {
    margin: 0 0 20px 0;
    max-width: 620px;
  }
  .scroll-prompt {
    bottom: 67px;
  }
  @media (max-width: 767px) {
    padding: 0 15px;
    font-size: 14px;
    line-height: 24px;
    height: auto;
  }
}

.clients-intro-wrapper {
  animation-duration: 1.5s;
}

.clients-intro-section {
  @media (max-width: 992px) {
    height: auto;
    padding-top: 50px;
  }
  @media (max-width: 767px) {
    margin-bottom: 50px;
  }
  h2 {
    @media (max-width: 767px) {
      margin-top: 80px;
      margin-bottom: 20px;
    }
  }
}

.clients-carousel-section {
  position: relative;
  text-align: center;
  overflow: hidden;
  height: 100vh;
  // display: flex;
  // flex-direction: column;
  // justify-content: center;
  // align-items: center;
  @media (max-width: 992px) {
    height: auto;
    padding-top: 50px;
    padding-bottom: 100px;
  }
  @media (max-width: 767px) {
    padding-top: 0;
    padding-bottom: 0;
    display: block;
    height: auto;
  }
  .slick-initialized.slick-slider.slick-dotted {
    margin-bottom: 0 !important;
  }
}

.clients-carousel {
  margin: 0 -60px;
  @media (max-width: 767px) {
    margin: 0;
  }
  .slick-dots {
    bottom: inherit;
    left: inherit;
    text-align: center;
    position: static;
    margin-top: 20px;
    @media (min-height: 767px) {
      margin-top: 42px;
    }
    @media (max-width: 767px) {
      margin-top: 40px;
      margin-bottom: 0;
    }
  }
  .slick-dots li {
    margin: 0 15px;
    @media (max-width: 767px) {
      margin: 0 10px;
    }
  }
  .slick-dots li button:before {
    width: 10px;
    height: 10px;
    background-color: transparent;
    border: 1px solid #979797;
    border-radius: 50%;
    color: transparent;
    opacity: 1;
  }
  .slick-initialized.slick-slider.slick-dotted {
    margin-bottom: 0 !important;
  }
  .slick-dotted.slick-slider {
    margin-bottom: 0 !important;
  }
}


li.slick-active > button:before {
  border-color: $primary !important;
}

.clients-carousel-slide {
  position: relative;
  margin: 0;
  @media (max-width: 767px) {
    width: auto;
  }
  img {
    display: inline-block;
    opacity: 0.5;
    transform: scale(0.8);
    transition: transform 300ms ease-in-out;
    height: 440px;
    @media (max-height: 800px) and (min-width: 767px) {
      height: 300px;
      margin: 0 80px;
    }
    @media (min-width: 1130px) and (min-height: 901px) {
      margin: 0 36px;
    }
    @media (max-height: 900px) and (min-width: 1130px) {
      margin: 0 90px;
    }
    @media (min-width: 1224px) and (min-height: 901px) {
      margin: 0 38px;
    }
    @media (max-height: 900px) and (min-width: 1224px) {
      margin: 0 92px;
    }
    @media (min-width: 1312px) and (min-height: 901px) {
      margin: 0 60px;
    }
    @media (max-height: 900px) and (min-width: 1312px) {
      margin: 0 150px;
    }
    @media (min-width: 1400px) and (min-height: 901px) {
      margin: 0 75px;
    }
    @media (max-height: 900px) and (min-width: 1400px) {
      margin: 0 170px;
    }
    @media (min-width: 1500px) and (min-height: 901px) {
      margin: 0 100px;
    }
    @media (max-height: 900px) and (min-width: 1500px) {
      margin: 0 190px;
    }
    @media (min-width: 1600px) and (min-height: 901px) {
      margin: 0 200px;
    }
    @media (max-height: 900px) and (min-width: 1600px) {
      margin: 0 253px;
    }
    @media (min-width: 1950px) and (min-height: 901px) {
      margin: 0 250px;
    }
    @media (max-height: 900px) and (min-width: 1950px) {
      margin: 0 306px;
    }
    @media (min-width: 2160px) and (min-height: 901px) {
      margin: 0 290px;
    }
    @media (max-height: 900px) and (min-width: 2160px) {
      margin: 0 343px;
    }
    @media (max-width: 767px) {
      height: 250px;
      max-width: 100%;
      transform: scale(1);
      opacity: 1;
      margin: 0 auto;
    }
  }
  h3 {
    margin-top: 0;
    margin-bottom: 30px;
    opacity: 0;
    transition: opacity 300ms ease-in-out;
    min-height: 60px;
    @media (max-width: 767px) {
      opacity: 1;
      font-size: 16px;
      padding: 0 15px;
      br {
        display: none;
      }
    }
  }
  &.first-slide {
    h3 {
      max-width: 460px;
      margin: 0 auto 30px auto;
    }
  }
  &.last-slide {
    h3 {
      visibility: visible !important;
      max-width: 230px;
      position: relative;
      left: 1px;
      top: 32px;
      @media (max-width: 767px) {
        line-height: 17px;
        max-width: 212px;
        top: 168px;
        left: 0;
      }
      @media (max-height: 800px) and (min-width: 767px) {
        max-width: 200px;
      }
      @media (min-height: 800px) and (min-width: 767px) {
        top: 50px;
      }
    }
    img {
      transform: scale(1) translateY(-5px);
      @media (max-width: 767px) {
        transform: scale(1) translateY(39px) !important;
      }
      @media (min-height: 800px) and (min-width: 767px) {
        transform: scale(1) translateY(15px) !important;
      }
    }
  }
}

.slide-flex-wrapper {
  position: absolute;
  top: 190px;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateY(-25px);
  @media (max-width: 767px) {
    top: -41px;
  }
}

.clients-carousel-img {
  position: relative;
  top: 85px;
  @media (max-width: 767px) {
    top: 58px;
  }
}

.clients-carousel-slide.slick-center {
  h3 {
    opacity: 1;
  }
  img {
    opacity: 1;
    transform: scale(1);
  }
}

.clients-carousel-text {
  color: #1C1D21;
  font-size: 16px;
  margin-top: 40px;
  margin-bottom: 50px;
  // @media (min-height: 767px) {
  //   margin-top: 40px;
  //   margin-bottom: 50px;
  // }
  @media (max-width: 767px) {
    margin-top: 40px;
    margin-bottom: 50px;
  }
}

.clients-btn {
  align-self: center;
  height: 50px;
  padding: 0 20px;
  border: 1px solid $primary;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: $primary;
  max-width: 170px;
  margin: 0 auto;
  text-decoration: none;
  @media (max-width: 767px) {
    width: 210px;
    margin: 0 auto 100px auto;
  }
  &:hover {
    border-width: 2px;
  }
  &:visited {
    color: $primary;
  }
  &:active {
    background-color: #E0E3F7;
  }
}

.Center-Container {
  height: 100vh;
  @media (max-width: 992px) {
    height: auto;
  }
}

.Center-Container.is-Table { display: table; }
.is-Table .Table-Cell {
  display: table-cell;
  vertical-align: middle;
}
.is-Table .Center-Block {
  width: 100vw;
  margin: 0 auto;
}
