
.manifesto-hero {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  @media (max-width: 992px) {
    padding: 50px;
    .scroll-prompt {
      display: block;
    }
  }
  @media (max-width: 767px) {
    padding: 0;
    text-align: center;
    margin-top: -65px;
    .scroll-prompt {
      bottom: 115px;
    }
    .scroll-prompt-text {
      display: block;
      bottom: 80px;
    }
  }
  @media (max-width: 736px) and (max-height: 414px) {
    .scroll-prompt {
      display: none;
    }
    .scroll-prompt-text {
      display: none;
    }
  }
  @media (max-height: 650px) and (min-width: 767px) {
    .scroll-prompt, .scroll-prompt-text {
      display: none !important;
    }
  }
  h2 {
    opacity: 0;
    margin-top: 73px;
    animation-name: basic-opacity;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;
    animation-delay: 1s;
    user-select: none;
    @media (max-width: 767px) {
      margin-top: 10px;
      margin-bottom: 100px;
    }
  }
}

.manifesto-hero-image {
  height: auto;
  max-width: 820px;
  opacity: 0;
  animation-name: basic-opacity;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  @media (max-width: 992px) {
    max-width: 100%;
    margin-top: 20px;
    height: auto;
  }
}

.manifesto-wrapper {
  width: 100%;
  animation-duration: 2s;
}

.manifesto-section {
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: $open-sans;
  font-size: 16px;
  line-height: 26px;
  color: #1C1D21;
  font-weight: lighter;
  text-align: center;
  @media (max-width: 992px) {
    height: auto;
    padding: 50px;
  }
  @media (max-width: 767px) {
    padding: 0 15px;
  }
  p {
    margin: 0;
  }
  h3 {
    font-family: 'Roboto Mono', monospace;
    margin-top: 0;
    &:nth-child(2) {
      margin-bottom: 0;
    }
    br {
      @media (max-width: 767px) {
        display: none;
      }
    }
  }
  .scroll-prompt {
    bottom: 67px;
  }
  &.position-top {
    p:last-of-type {
      margin-bottom: 70px;
      @media (max-width: 992px) {
        margin-bottom: 0;
      }
    }
  }
}

.manifesto-intro-section {
  h3 {
    max-width: 622px;
    margin-left: auto;
    margin-right: auto;
  }
}

.manifesto-image-2 {
  margin-top: 49px;
  margin-bottom: 46px;
  max-width: 570px;
  @media (max-width: 767px) {
    display: none;
  }
}

.manifesto-image-2-mobile {
  display: none;
  @media (max-width: 767px) {
    display: block;
    margin-top: 40px;
    margin-bottom: 40px;
    margin-left: auto;
    margin-right: auto;
    max-width: 275.57px;
  }
}

.manifesto-pillars-section {
  h3 {
    font-family: 'Roboto Mono', monospace;
    max-width: 619px;
    margin: 0;
    @media (max-width: 767px) {
      max-width: 260px;
    }
  }
  @media (max-width: 767px) {
    margin-top: 80px;
  }
}

.manifesto-pillars {
  margin-top: 90px;
  margin-bottom: 90px;
  @media (max-width: 992px) {
    margin-bottom: 0;
  }
  @media (max-width: 767px) {
    margin-top: 40px;
    margin-bottom: 80px;
    display: flex;
  }
}

.manifesto-pillar {
  position: relative;
  width: 246px;
  height: 246px;
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
  display: inline-block;
  border-radius: 50%;
  animation-fill-mode: forwards;
  animation-duration: 0.3s;
  animation-timing-function: ease-in;
  @media (max-width: 992px) {
    width: 200px;
    height: 200px;
  }
  @media (max-width: 767px) {
    width: 106px;
    height: 105px;
    img {
      width: 15px;
      height: 15px;
      margin: 0 auto;
    }
  }
}

@keyframes pillar-value {
  0% {
    border-color: transparent;
  }
  100% {
    border-color: $secondary-green;
  }
}

@keyframes pillar-meaning {
  0% {
    border-color: transparent;
  }
  100% {
    border-color: $primary;
  }
}

@keyframes pillar-engagement {
  0% {
    border-color: transparent;
  }
  100% {
    border-color: $secondary-yellow;
  }
}

.pillar-value {
  border-color: transparent;
  position: relative;
  left: 28px;
  animation-name: pillar-value;
  animation-delay: 0.5s;
  @media (max-width: 767px) {
    left: 14px;
  }
}

.pillar-meaning {
  border-color: transparent;
  animation-name: pillar-meaning;
  animation-delay: 1.5s;
}

.pillar-engagement {
  border-color: transparent;
  position: relative;
  right: 28px;
  animation-name: pillar-engagement;
  animation-delay: 2.5s;
  @media (max-width: 767px) {
    right: 14px;
  }
}

.manifesto-pillar-content {
  opacity: 0;
  display: flex;
  flex-direction: column;
  position: relative;
  top: 100px;
  animation: basic-opacity;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  @media (max-width: 992px) {
    top: 80px;
  }
  @media (max-width: 767px) {
    top: 44px;
  }
  &.meaning {
    animation-delay: 1s;
  }
  &.engagement {
    animation-delay: 2s;
  }
  img {
    align-self: center;
  }
}

.pillar-title {
  font-weight: lighter;
  font-size: 16px;
  color: #4A4A4A;
  margin-top: 18px;
  @media (max-width: 767px) {
    font-size: 12px;
    line-height: 0;
    margin-top: 12px;
  }
}

