.circle-icon-container {
  opacity: 0;
  position: absolute;
  animation-name: basic-opacity;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  @media (max-width: 767px) {
    animation: none;
    opacity: 1;
  }
  img {
    margin-bottom: 10px;
    @media (max-width: 767px) {
      margin-bottom: 6px;
    }
  }

  &.smell {
    top: -54px;
    left: 0;
    right: 0;
    animation-delay: 0.3s;
    @media (max-width: 767px) {
      bottom: inherit;
      right: inherit;
      left: 48px;
      top: 93px;
    }
  }
  &.touch {
    top: 132px;
    right: -53px;
    animation-delay: 0.6s;
    @media (max-width: 767px) {
      bottom: inherit;
      right: 48px;
      top: 93px;
    }
  }
  &.taste {
    bottom: -6px;
    right: 28px;
    animation-delay: 1s;
    @media (max-width: 767px) {
      bottom: 56px;
      right: 76px;
    }
  }
  &.hearing {
    bottom: -6px;
    left: 28px;
    animation-delay: 1.6s;
    @media (max-width: 767px) {
      bottom: 58px;
      left: 68px;
    }
  }
  &.sight {
    top: 123px;
    left: -53px;
    animation-delay: 2.4s;
    @media (max-width: 767px) {
      top: 41px;
      left: 111px;
    }
  }
}

