.manifesto-promise {
  position: relative;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: $open-sans;
  font-weight: lighter;
  font-size: 16px;
  @media (max-width: 992px) {
    height: auto;
    padding: 50px;
  }
  @media (max-width: 767px) {
    padding: 80px 15px;
    line-height: 24px;
    font-size: 14px;
  }
  p {
    margin: 0;
    &:last-of-type {
      max-width: 549px;
    }
  }
  .scroll-prompt {
    bottom: 67px;
  }
}

.manifesto-promise-quote {
  position: relative;
  font-size: 20px;
  color: $primary;
  line-height: 30px;
  font-weight: lighter;
  max-width: 616px;
  margin-top: 71px;
  margin-bottom: 71px;
  font-family: 'Roboto Mono', monospace;
  animation-name: quote-pulse;
  animation-duration: 2s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  transform: scale(0.5);
  opacity: 0;
  @media (max-width: 767px) {
    margin: 72px 0;
  }
  &::after, &::before {
    position: absolute;
    content: '';
    display: block;
    width: 13px;
    height: 10px;
    background-repeat: no-repeat;
  }
  &::after {
    background-image: url('../images/promise-quote-1.svg');
    top: -30px;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  &::before {
    background-image: url('../images/promise-quote-2.svg');
    bottom: -30px;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
}

@keyframes quote-pulse {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.manifesto-bios {
