}

.bio-box-title {
  @media (max-width: 767px) {
    margin-bottom: 20px;
  }
}

.bio-box-text {
  margin-top: 15px;
  @media (max-width: 767px) {
    font-size: 14px;
  }
}

.bio-box-socials {
  margin-top: 25px;
  @media (max-width: 767px) {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

.bio-box-social {
  display: inline-block;
  width: 25px;
  height: 25px;
  background-repeat: no-repeat;
  background-size: contain;
}

.bio-box-twitter {
  background-image: url('../images/bio-twitter.svg');
  margin-right: 15px;
}

.bio-box-linkedin {
  background-image: url('../images/bio-linkedin.svg');
}

.manifesto-cta {
  display: block;
  height: 50px;
  font-size: 20px;
  color: $primary;
  border: 1px solid $primary;
  padding: 0 20px;
  display: flex;
  align-items: center;
  margin-top: 70px;
  text-decoration: none;
  @media (max-width: 767px) {
    margin-bottom: 100px;
  }
  &:hover {
    border-width: 2px;
  }
  &:visited {
    color: $primary;
  }
  &:active {
    background-color: #E0E3F7;
  }
}

.half-globe-container {
  position: relative;
  max-width: 1024px;
  margin: 0 auto;
  height: 100%;
}
